:root{
  --text:snow;
  --bg:black;
  --img:url('./imgs/arian-darvishi-wh-RPfR_3_M-unsplash.jpg');
  --hgt:85vh;
  --profile:url('./imgs/sivaprakasam.jpg');
  --glass1: #ffffff35;
  --glass2: #ffffff25;
  --glass3: #ffffff05;
  --border: #ffffff7b;
}
/* Glass plate */
.Glass-plate{
  background: linear-gradient(to top left,var(--glass3) 20%,var(--glass2) 80%,var(--glass1) 90%);
  /* box-shadow: 0px 0px 0px 1px var(--border); */
  padding: 0 0.5rem;
  height: fit-content;
  width: fit-content;
  border-radius: 0.5rem;
  color:var(--text);
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
}

html {
  scroll-behavior: smooth;
}

body{
  background-image: var(--img);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
}

/* Headre and Footer */
header,footer{
  justify-content: center;
  /* background-image: url('./imgs/wallpaperflare.com_wallpaper.jpg'); */
  display: flex;
  position: fixed;
  width: 100%;
  color: var(--text);
  z-index: 100;
}

header{
  top: 0;
}

footer{
  bottom: 0;
  justify-content: center;
}

.mtitle{
  line-height: 0;
}

nav{
  margin: auto;
  align-self: flex-end;
  display: flex;
  justify-content: space-around;
  width: 50%;
  cursor: pointer;
}

nav .a{
  padding: 0.5rem 1rem;
  text-decoration: none;
  font-weight: bolder;
  box-shadow: 0 0.1rem 0 var(--text);
  transition: all 0.5s;
  color: var(--text);
}

nav .a:hover,.active{
  color: var(--bg);
  box-shadow: 0 0.1rem 0 var(--text),0rem -2rem 0.5rem -0.5rem var(--text) inset;
}

/* Main */
.bg,main{
  position: fixed;
  top:50%;
  left: 50%;
  background-image: var(--img);
  transform: translate(-50%, -50%);
  width: 100vw;
  height: var(--hgt);
  background-size: cover;
  background-position: center;
  filter: blur(3px);
}
main{
  background: transparent;
  filter: none;
  width: 100vw;
  height: var(--hgt);
  box-shadow: 0 0 1rem 0 black;
  overflow-y: hidden;
  overflow-x: hidden;
  z-index: 50;
  transition: all 0.5s;
  scroll-behavior: smooth;
}

th h2{
  line-height: 0;
}

/* Containers */
.container{
  display: grid;
  grid-template-columns: 50% 50%;
  grid-gap: 1rem;
  color: var(--text);
  width: 100vw;
  height: var(--hgt);
  overflow-y: auto;
  overflow-x: hidden;
}

.container > div{
  padding: 0.5rem;
  flex-direction: column;
  display: flex;
}

.container h1,.container h3{
  text-align: left;
}

h1 a,h4 a{
  color:snow;
  text-decoration: none;
}

h4{
  background: linear-gradient(to top left,var(--glass3) 20%,var(--glass2) 80%,var(--glass1) 90%);
  box-shadow: 0px 0px 0px 1px var(--border);
  border-radius: 0.5rem;
  width: fit-content;
  color:var(--text);
  padding: 0.3rem;
}

/* Profile */

.profile{
  width: 10rem;
  height: 10rem;
  border-radius: 100%;
  box-shadow: 0px 0px 0.5rem 0px black inset;
  background-image: var(--profile);
  background-repeat: no-repeat;
  background-size: cover;
}

/* Icon */

.icon {
  background: linear-gradient(to top left,var(--glass3) 20%,var(--glass2) 80%,var(--glass1) 90%);
  border: none;
  outline: none;
  padding: 0.2rem;
  width: 2rem;
  height: 2rem;
  border-radius: 100%;
  transform: scale(0.95);
  cursor: pointer;
  stroke: var(--text);
  margin: 0 0.5rem;
}

.icon:hover{
  background: linear-gradient(to top left,var(--glass1) 20%,var(--glass2) 80%,var(--glass3) 90%);
  transform: scale(1);
}

.right-bottom{
  position: fixed;
  margin: auto;
  left: auto;
  top:auto;
  right: 0.5rem;
  bottom: 0.5rem;
  z-index: 100;
  width: 50px;
}

.margin-auto{
  margin: auto;
}

/* Button */

.button{
  padding: 1rem 0.5rem;
  background: linear-gradient(to top left,var(--glass1) 20%,var(--glass2) 80%,var(--glass3) 90%);
  box-shadow: 0 0 0.1rem 0 var(--border) inset;
  text-align: center;
  border-radius: 0.3rem;
  text-decoration: none;
  color: var(--text);
  font-size: large;
  font-weight: bolder;
}

.img-size{
  padding: 0 0.5rem;
  width: 1.5rem;
  height: 1rem;
}

/* Table */

.table,.stable{
  align-self: center;
}

.table td{
  padding: 0.5rem;
}

.stable th{
  font-weight: bold;
  text-align: left;
} 

.stable td{
  padding: 0 3rem;
  font-size: 110%;
}

/* Skills */

/* Skill Show */

.lskill-set{
  width: 80%;
  display: flex;
  margin: 0.5rem;
  justify-content: space-around;
}

.slogo{
  margin: auto;
  height: 3rem;
  width: 3rem;
}

.percont{
  font-size: small;
  width: 90%;
}

.lskill-per{
  box-shadow: 0px 0px 0.5rem 0px var(--glass1) inset;
  overflow: hidden;
  float:left;
  height: 0.5rem;
  width: 100%;
  border-radius: 0.3rem;
}

.score{
  background: rgba(255, 250, 250, 0.7);
  height: 100%;
  float: left;
}

/* Score */

.score-80{
  width: 80%;
}

.score-70{
  width: 70%;
}

.score-60{
  width: 60%;
}

.score-50{
  width: 50%;
}

.score-10{
  width: 10%;
}

/* Mobile */
@media screen and (max-width: 426px){
  :root{
    --hgt:80vh;
  }
  nav{
    width:100%;
  }
  .container{
    grid-gap: 0rem;
    grid-template-columns: none;
    grid-template-rows: auto auto;
  }
}